import axios from "axios";//intalar y importar axios
const pokemones={
    namespaced:true,
    state:{
        prueba:'Paola',
        datosPokemones:{},
        mostrarImagen: false

    },
    getters:{},
    mutations:{
        CONSULTAR_POKEMON:(state, pokemones)=>{
            // if(nombrePokemon === '' || nombrePokemon === null || nombrePokemon === )
            state.datosPokemones= pokemones;
        },
        MOSTRAR_IMAGEN:(state)=>{
            state.mostrarImagen = true
        },
        OCULTAR_IMAGEN:(state)=>{
            state.mostrarImagen = false
        }
    },
    actions:{
        consultarPokemon:(context, nombrePokemon)=>{//context invoca los mutations 
            context.commit('OCULTAR_IMAGEN');
            let urlPoke = 'https://pokeapi.co/api/v2/pokemon/'+nombrePokemon;
            axios.get(urlPoke)
            .then(respuesta=>{
                console.log(respuesta);
                context.commit('CONSULTAR_POKEMON', respuesta.data);
                context.commit('MOSTRAR_IMAGEN');
            })
            .catch(error=>{
                console.log(error)
            })

        }
    },
    modules:{}
}
export default pokemones;